@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
    -webkit-margin-before: 0rem;
            margin-block-start: 0rem;
    -webkit-margin-after: 0rem;
            margin-block-end: 0rem;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}
li {
  text-decoration: none;
  list-style-type: none;
}
a {
  padding: 0.3rem 0.3rem 0.3rem 0.3rem;
  border-radius: 0.25rem;
}
a:hover {
  background-color: lightblue;
  cursor: pointer;
}
button {
  padding: 0.5rem 1rem 0.5rem 1rem;
  outline:none;
  border: none;
}
button:hover {
  opacity: 50%;
}
/* General */
.screen {
  height: 100vh;
  width: 100%;
  z-index: 9;
  background-color: lightgrey;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.list-container {

}
.item-container {
  /* border: 1px red dashed; */
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 0 1rem 0 1rem;
}
.pic-container {
  /* border: 1px blue dashed; */
  display: flex;
  justify-content: center;
  align-items: center;
}

div[class*="nav-box"] ul {
    display:flex;
    flex-direction: row;
    align-items: center;
}
div[class*="nav-box"] ul li {
    margin: 0 0.7rem 0 0.7rem;
}
ul[class*="messages-list"] li {
    display: flex;
    justify-content: flex-start;
    padding: 1rem 1rem 1rem 1rem;
    border-bottom: 1px rgb(170, 170, 170) solid;
}
.welcome-statement {
    background-color:rgb(216, 216, 216);
}
ul[class*="add-list"] li:not(:last-child) {
    border-bottom: 1px rgb(170, 170, 170) solid;
}
/* Message */
.message-new {
    height: 0.95rem;
    width: 0.95rem;
    background-color: red;
    border-radius: 0.2rem;
}
.message-received {
    height: 0.6rem;
    width: 0.6rem;
    background-color: none;
    border: 0.17rem solid red;
    border-radius: 0.2rem;
}
.message-opened {
    width: 0.9rem;
	height: 0.9rem;
    background-image: url(/static/media/opened-icon.c995714b.png);
    background-repeat: no-repeat;
    background-size: 0.9rem 0.9rem;
    background-position: center;
	/* border-top: 0.5rem solid transparent;
	border-left: 1rem solid black;
	border-bottom: 0.5rem solid transparent; */
}
.message-sent {
    width: 0.9rem;
	height: 0.9rem;
    background-image: url(/static/media/sent-icon.ec0981e8.png);
    background-repeat: no-repeat;
    background-size: 0.9rem 0.9rem;
    background-position: center;
	/* border-top: 0.5rem solid transparent;
	border-left: 1rem solid #664db1;
	border-bottom: 0.5rem solid transparent; */
}
.message-new-friend {
    height: 0.6rem;
    width: 0.6rem;
    background-color: none;
    border: 0.17rem solid lightgrey;
    border-radius: 0.2rem;
}
.message-info {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.message-info > * {
    margin: 0 0.1rem 0 0.1rem;
}
.webcam-screen {
    height:100vh;
    overflow-y: hidden;
    overflow-x: hidden;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: white; */
    flex-direction: column;
}
.image-desktop {
    width: 100%;
}
.app-nav {
  position: absolute;
  top:0;
  z-index: 10;
  color: black;
  background-color: white;
  color: black;
}
.navbar {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    justify-content: space-between;
    height: 2.5rem;
}
.nav-profile-pic {
  height: 2rem;
  width:2rem;
  border-radius: 1rem;
}
.nav-profile-pic:hover {
  opacity: 80%;
}

/* Profile Section */
.profile-pic {
  border-radius: 1rem;
  width:8rem;
  margin:1rem
}

/* Add Friend Section */
.friend-profile-pic {
  border-radius: 1rem;
  width:4rem;
}
.friend-info {
  /* border: 1px green dashed; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 0.5rem 0 0.5rem;
}
.friend-button {
  /* border: 1px orange dashed; */
  display: flex;
  align-items: center;
}
.friend-button button {
  border-radius: 1rem;
}
.removed-friend {
  background-color: rgb(255, 126, 126);
}

.app-foot {
  position: absolute;
  bottom:0;
  z-index: 8;
  background-color: black;
  color: white;
}
.footer {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  justify-content: space-evenly;
  height: 2.5rem;
}
